/*
┬─┐┌─┐┌─┐┌─┐┌┬┐
├┬┘├┤ └─┐├┤  │
┴└─└─┘└─┘└─┘ ┴
*/

@use 'sass:map';
@use 'functions' as functions;

$config: nil !default;
$theme: nil !default;

$prefix: functions.get-config($config, 'prefix');
$smooth-scroll: functions.get-config($config, 'enable', 'smooth-scroll');

$body-font-family: functions.get-config($config, 'html', 'body', 'font', 'default');
$body-font-size: functions.get-config($config, 'html', 'body', 'font', 'size');
$body-color: functions.get-config($theme, 'body', 'fonts', 'size');

$headings-font-family: functions.get-config($theme, 'typography', 'headings', 'font', 'family');
$headings-font-style: functions.get-config($theme, 'typography', 'headings', 'font', 'style');
$headings-font-weight: functions.get-config($theme, 'typography', 'headings', 'font', 'family');
$headings-line-height: functions.get-config($theme, 'typography', 'headings', 'font', 'line-height');

$h1-font-size: functions.get-config($theme, 'html', 'h1', 'font', 'size');
$h2-font-size: functions.get-config($theme, 'html', 'h2', 'font', 'size');
$h3-font-size: functions.get-config($theme, 'html', 'h3', 'font', 'size');
$h4-font-size: functions.get-config($theme, 'html', 'h4', 'font', 'size');
$h5-font-size: functions.get-config($theme, 'html', 'h5', 'font', 'size');
$h6-font-size: functions.get-config($theme, 'html', 'h6', 'font', 'size');

$paragraph-margin: functions.get-config($theme, 'html', 'p', 'margin');

$abbr-text-decoration: functions.get-config($theme, 'html', 'abbr', 'attributes', 'title', 'text-decoration');
$abbr-cursor: functions.get-config($theme, 'html', 'abbr', 'attributes', 'title', 'text-decoration');
$abbr-text-decoration-skip-ink: functions.get-config($theme, 'html', 'abbr', 'attributes', 'title', 'text-decoration-skip-ink');

$address-margin: functions.get-config($theme, 'html', 'address', 'margin');
$address-font-style: functions.get-config($theme, 'html', 'address', 'font', 'style');
$address-line-height: functions.get-config($theme, 'html', 'address', 'font', 'line-height');

$blockquote-margin: functions.get-config($theme, 'html', 'blockquote', 'margin');

$small-font-size: functions.get-config($theme, 'html', 'small', 'font', 'size');

$mark-padding: functions.get-config($theme, 'html', 'mark', 'padding');

$sub-position: functions.get-config($theme, 'html', 'sub', 'position');
$sub-font-size: functions.get-config($theme, 'html', 'sub', 'font', 'size');
$sub-line-height: functions.get-config($theme, 'html', 'sub', 'font', 'line-height');
$sub-vertical-align: functions.get-config($theme, 'html', 'sub', 'vertical-align');
$sub-bottom: functions.get-config($theme, 'html', 'sub', 'bottom');

$sup-position: functions.get-config($theme, 'html', 'sup', 'position');
$sup-font-size: functions.get-config($theme, 'html', 'sup', 'font', 'size');
$sup-line-height: functions.get-config($theme, 'html', 'sup', 'font', 'line-height');
$sup-vertical-align: functions.get-config($theme, 'html', 'sup', 'vertical-align');
$sup-top: functions.get-config($theme, 'html', 'sup', 'top');

$a-text-decoration: functions.get-config($theme, 'html', 'a', 'text-decoration');
$a-hover-text-decoration: functions.get-config($theme, 'html', 'a', 'pseudo', 'hover', 'text-decoration');

$figure-margin: functions.get-config($theme, 'html', 'figure', 'margin');

$svg-vertical-align: functions.get-config($theme, 'html', 'svg', 'vertical-align');
$img-vertical-align: functions.get-config($theme, 'html', 'img', 'vertical-align');

$table-caption-side : functions.get-config($theme, 'html', 'table', 'caption-side');
$table-border-collapse : functions.get-config($theme, 'html', 'table', 'border-collapse');


*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  @if $smooth-scroll {
    @media (prefers-reduced-motion: no-preference) {
      scroll-behavior: smooth;
    }
  }
}

// Body
// scss-docs-start reboot-body-rules
body {
  padding: 0;
  margin: 0;
  font-family: $body-font-family;
  font-size: $body-font-size;

  font-weight: var(--#{$prefix}body-font-weight);
  line-height: var(--#{$prefix}body-line-height);
  color: var(--#{$prefix}body-color);
  text-align: var(--#{$prefix}body-text-align);
  background-color: var(--#{$prefix}body-bg);

  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

// scss-docs-end reboot-body-rules

// Typography
%heading {
  margin: 0;
  font-family: $headings-font-family;
  font-style: $headings-font-style;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
}

h1 {
  @extend %heading;
  font-size: $h1-font-size;
}

h2 {
  @extend %heading;
  font-size: $h2-font-size;
}

h3 {
  @extend %heading;
  font-size: $h3-font-size;
}

h4 {
  @extend %heading;
  font-size: $h4-font-size;
}

h5 {
  @extend %heading;
  font-size: $h5-font-size;
}

h6 {
  @extend %heading;
  font-size: $h6-font-size;
}

// Paragraphs
p {
  margin: $paragraph-margin;
}


// Abbreviations
abbr[title] {
  text-decoration: $abbr-text-decoration;
  cursor: $abbr-cursor;
  text-decoration-skip-ink: $abbr-text-decoration-skip-ink;
}

// Address
address {
  margin-bottom: $address-margin;
  font-style: $address-font-style;
  line-height: $address-line-height;
}

// Lists
ol,
ul,
dl {
  padding: 0;
  margin: 0;
}

dt {
  font-weight: 600;
}

dd {
  margin: 0;
}


// Blockquote
blockquote {
  margin: $blockquote-margin;
}


// Strong
b,
strong {
  font-weight: 700;
}


// Small
small {
  font-size: $small-font-size;
}


// Mark
mark {
  padding: $mark-padding;
  //background-color: var(--#{config.$prefix}highlight-bg);
}


// Sub and Sup
sub {
  position: $sub-position;
  font-size: $sub-font-size;
  line-height: $sub-line-height;
  vertical-align: $sub-vertical-align;
  bottom: $sub-bottom;
}

sup {
  position: $sup-position;
  font-size: $sup-font-size;
  line-height: $sup-line-height;
  vertical-align: $sup-vertical-align;
  top: $sup-top;
}

// Links
a {
  //color: var(--#{config.$prefix}link-color);
  text-decoration: $a-text-decoration;

  &:hover {
    //color: var(--#{config.$prefix}link-hover-color);
    text-decoration: $a-hover-text-decoration;
  }
}

// Links with empty "href" attribute will not be highlighted
a:not([href]):not([class]) {
  &,
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}


// Code

pre,
code,
kbd,
samp {
  //font-family: config.$font-family-code;
  font-size: 1em
}

pre {
  display: block;
  margin-top: 0; // 1
  margin-bottom: 1rem; // 2
  overflow: auto; // 3
  //font-size: config.$code-font-size;
  //color: config.$pre-color;

  // Account for some code outputs that place code tags in pre tags
  code {
    font-size: inherit;
    color: inherit;
    word-break: normal;
  }
}

code {
  //font-size: config.$code-font-size;
  //color: var(--#{config.$prefix}code-color);
  word-wrap: break-word;

  // Streamline the style when inside anchors to avoid broken underline and more
  a > & {
    color: inherit;
  }
}

kbd {
  //padding: config.$kbd-padding-y config.$kbd-padding-x;
  //font-size: config.$kbd-font-size;
  //color: config.$kbd-color;
  //background-color: config.$kbd-bg;

  kbd {
    padding: 0;
    font-size: 1em;
  }
}


// Figures
figure {
  margin: $figure-margin;
}


// Images and content
img {
  vertical-align: $img-vertical-align;
}

svg {
  vertical-align: $svg-vertical-align;
}


// Tables
table {
  caption-side: bottom;
  border-collapse: collapse;
}

caption {
  //padding-top: config.$table-cell-padding-y;
  //padding-bottom: config.$table-cell-padding-y;
  //color: config.$table-caption-color;
  text-align: left;
}

th {
  //font-weight: config.$table-th-font-weight;
  text-align: inherit;
  text-align: -webkit-match-parent;
}

thead,
tbody,
tfoot,
tr,
td,
th {
  border-color: inherit;
  border-style: solid;
  border-width: 0;
}


// Forms
label {
  display: inline-block;
}

button {
  border-radius: 0;
}

button:focus:not(:focus-visible) {
  outline: 0;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  background: transparent;
}

button,
select {
  text-transform: none;
}

[role="button"] {
  cursor: pointer;
}

select {
  word-wrap: normal;

  &:disabled {
    opacity: 1;
  }
}

[list]:not([type="date"]):not([type="datetime-local"]):not([type="month"]):not([type="week"]):not([type="time"])::-webkit-calendar-picker-indicator {
  display: none !important;
}

button,
[type="button"], // 1
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // 2

  //@if config.$enable-button-pointers {
  //  &:not(:disabled) {
  //    cursor: pointer; // 3
  //  }
  //}
}

::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

// Textarea
textarea {
  resize: vertical;
}

// Fieldset
fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

// Legend
legend {
  float: left; // 1
  width: 100%;
  padding: 0;
  //margin-bottom: config.$legend-margin-bottom;
  //font-size: config.$legend-font-size;
  //font-weight: config.$legend-font-weight;
  line-height: inherit;

  + * {
    clear: left; // 2
  }
}

::-webkit-datetime-edit-fields-wrapper,
::-webkit-datetime-edit-text,
::-webkit-datetime-edit-minute,
::-webkit-datetime-edit-hour-field,
::-webkit-datetime-edit-day-field,
::-webkit-datetime-edit-month-field,
::-webkit-datetime-edit-year-field {
  padding: 0;
}

::-webkit-inner-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-color-swatch-wrapper {
  padding: 0;
}

::file-selector-button {
  font: inherit;
  -webkit-appearance: button;
}

// Output
output {
  display: inline-block;
}

// iFrame
iframe {
  border: 0;
}

// HR
hr {
  margin: 0;
  border: 0;
  color: transparent;
  //border-top: config.$hr-border-width solid config.$hr-border-color;
}

// Summary
summary {
  display: list-item;
  cursor: pointer;
}

// Progress
progress {
  vertical-align: baseline;
}

// Hidden attribute
[hidden] {
  display: none !important;
}
